var that;
import uniGrid from '@/components/uni-grid/uni-grid.vue'
import uniGridItem from '@/components/uni-grid-item/uni-grid-item.vue'
export default {
	components: {
		uniGrid,
		uniGridItem
	},
	data() {
		return {
			tupian: 'https://zzwl1zsxn.oss-cn-hangzhou.aliyuncs.com/images/2020/03/20/fe6861f82870aa9826f7e01ea8878235.png',
			nicheng: '请先登录',
			count: 0
		}
	},
	onLoad: function(option) {
		that = this;
	},
	onShow: function(res) {

		uni.request({
			url: this.$api_url + '/user/get_user_info', //仅为示例，并非真实接口地址。
			method: 'GET',
			header: {
				// 'Content-Type': 'application/x-www-form-urlencoded',
				'authorization': uni.getStorageSync('authorization') //自定义请求头信息
			},
			success: res => {
				console.log(res)
				if (res.data.code == 1000) {
					that.nicheng = res.data.content.userName;
					that.tupian = res.data.content.headimg;
				}else if(res.data.code == 2002){
					try {
						uni.clearStorageSync();
					} catch (e) {
						console.log('清除缓存错误：' + e);
					}
				}
			}
		});


		uni.request({
			url: this.$api_url + '/notify/count', //仅为示例，并非真实接口地址。
			method: 'GET',
			header: {
				// 'Content-Type': 'application/x-www-form-urlencoded',
				'authorization': uni.getStorageSync('authorization') //自定义请求头信息
			},
			// data: {
			// 	openid: uni.getStorageSync('openid')
			// },
			success: res => {
				console.log(res)
				if (res.data.code == 1000) {
					that.count = res.data.content;
				}else if(res.data.code == 2002){
					try {
						uni.clearStorageSync();
					} catch (e) {
						console.log('清除缓存错误：' + e);
					}
				}
			}
		});

	},
	methods: {
		setting: function() {

			var token = uni.getStorageSync('authorization');
			if (token == '' || token == null) {
				uni.showModal({
					cancelText: '取消登录',
					confirmText: '立即登录',
					title: '提示',
					content: '是否确定授权登录？',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: './login'
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
				return
			}

			uni.navigateTo({
				url: '/pages/setter/setter'
			});

		},
		info: function() {

			var token = uni.getStorageSync('authorization');
			if (token == '' || token == null) {
				uni.showModal({
					cancelText: '取消登录',
					confirmText: '立即登录',
					title: '提示',
					content: '是否确定授权登录？',
					success: function(res) {
						if (res.confirm) {
							uni.navigateTo({
								url: './login'
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
				return
			}

			uni.navigateTo({
				url: './changinfo'
			});

		},
		change: function(e) {

			var token = uni.getStorageSync('authorization');
			if (token == '' || token == null) {

				return
			}

			let {
				index
			} = e.detail
			console.log(index)
			if (0 == index) {
				uni.navigateTo({
					url: './conpon?type=0'
				})
			} else if (1 == index) {
				uni.chooseAddress({
					success(res) {
						console.log(res)
						console.log(res.postalCode)
						console.log(res.provinceName)
						console.log(res.cityName)
						console.log(res.countyName)
						console.log(res.detailInfo)
						console.log(res.nationalCode)
						console.log(res.telNumber)

					}
				})
			} else {
				uni.navigateTo({
					url: './systemmessage'
				})
			}
			// if (3 == index) {
			// 	uni.navigateTo({
			// 		url: './systemmessage'
			// 	})
			// }
		},
		changexinxi: function(e) {
			var token = uni.getStorageSync('authorization');
			if (token == '' || token == null) {

				return
			}
			let {
				index
			} = e.detail
			console.log(index)
			if (0 == index) {
				uni.navigateTo({
					url: './information'
				})
			}
			if (1 == index) {
				uni.showModal({
					title: '提示',
					content: '该功能正在研发中...敬请期待！',
					showCancel: false,
					confirmColor: '#65c186',
					success: function(res) {
						if (res.confirm) {
							console.log('用户点击确定');
						}
					}
				});

			}
			if (2 == index) {
				uni.navigateTo({
					url: '../find/wallet'
				})
			}
			if (3 == index) {
				uni.navigateTo({
					url: './collect'
				})
			}
		},
		changedingdan: function(e) {
			var token = uni.getStorageSync('authorization');
			if (token == '' || token == null) {

				return
			}
			let {
				index
			} = e.detail
			console.log(index)
			that.order(index);
		},
		changewenda: function(e) {
			let {
				index
			} = e.detail
			uni.showModal({
				title: '提示',
				content: '该功能正在研发中...敬请期待！',
				showCancel: false,
				confirmColor: '#65c186',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
					}
				}
			});

			// uni.downloadFile({
			// 	url: 'https://zzwl1zsxn.oss-cn-hangzhou.aliyuncs.com/pdfFiles/2020-04-14469.png', //仅为示例，并非真实的资源
			// 	success: (res) => {
			// 		console.log('分享海报' + res.tempFilePath);
			// 		if (res.statusCode === 200) {
			// 			uni.saveImageToPhotosAlbum({
			// 				filePath: res.tempFilePath,
			// 				success: function(res) {
			// 					var savedFilePath = res.savedFilePath;
			// 					console.log('分享海报保存成功' + savedFilePath);
			// 				}
			// 			});
			// 		}
			// 	},
			// 	fail: (e) => {
			// 		console.log(e)
			// 	}
			// });


		},

		order: function(e) {
			var token = uni.getStorageSync('authorization');
			if (token == '' || token == null) {

				return
			}
			uni.navigateTo({
				url: '/pages/order/order?state=' + e
			});
		},
	}
}
